<template>
  <div class="sign-wrap">
    <div class="boardBox" ref="boardBox" @touchmove.prevent>
      <canvas
        ref="canvas"
        :width="cWidth"
        :height="cHeight"
        id="canvas"
        @touchstart="mStart"
        @touchmove="mMove"
        @touchend="mEnd"
      ></canvas>
    </div>
    <div class="footer-bar">
      <div class="reset" @click="clearcanvas"></div>
      <div class="use" @click="getcanvas"></div>
    </div>
  </div>
</template>
<script>
export default {
  name: "Sign",
  components: {},
  data() {
    return {
      cWidth: 1000,
      cHeight: 1000,
      ctx: null,
      point: {
        x: 0,
        y: 0,
      },
    };
  },
  mounted() {
    this.ctx = this.$refs.canvas.getContext("2d"); // 
    this.ctx.strokeStyle = "#000"; // 
  },
  methods: {
    // ()
    mStart(e) {
      let x = e.touches[0].clientX - e.target.offsetLeft,
        y = e.touches[0].clientY - e.target.offsetTop; // canvas
      this.point.x = x;
      this.point.y = y;
      this.ctx.beginPath();
      this.ctx.lineWidth = 7; // 
      this.moving = true;
    },
    // ...
    mMove(e) {
      if (this.moving) {
        let x = e.touches[0].clientX - e.target.offsetLeft,
          y = e.touches[0].clientY - e.target.offsetTop; // canvas
        this.ctx.moveTo(this.point.x, this.point.y); // ()
        this.ctx.lineTo(x, y); // 
        this.ctx.stroke(); // 
        (this.point.x = x), (this.point.y = y); // 
      }
    },
    // 
    mEnd() {
      if (this.moving) {
        this.ctx.closePath(); // 
        this.moving = false; // 
      }
    },

    //
    clearcanvas() {
      var ctx = document.getElementById("canvas").getContext("2d");
      //  c.getContext("2d");
      ctx.setTransform(1, 0, 0, 1, 0, 0);
      ctx.clearRect(0, 0, ctx.canvas.width, ctx.canvas.height);
      // this.$myMsg.notify({
      //   content: "",
      //   type: "success",
      // });
    },

    // 
    isCanvasEmpty(canvas) {
      var blank = document.createElement("canvas"); //canvas
      blank.width = canvas.width;
      blank.height = canvas.height;
      return canvas.toDataURL() == blank.toDataURL(); //
    },

    //
    getcanvas() {
      //
      // let canvas =  document.getElementById("canvas").toDataURL("image/png");
      let canvas = document.getElementById("canvas");
      if (this.isCanvasEmpty(canvas)) {
        // alert("");
        this.$Message.notify({
          content: "",
          type: "error",
          //time: 5500
        });
        return;
      }
    //   this.convertCanvasToImage();
    console.log(this.$refs.canvas.toDataURL("image/png"))
    // 
    this.basedata = this.$refs.canvas.toDataURL("image/png")
    this.updataImg()
    },

    //
    async updataImg() {
     
      //
      let data = {
        code: "hand_sign/hand_img_upload",
        data: JSON.stringify({
          verifyId: "18668011733",
          handImg: this.basedata,
        }),
      };
      await this.$Axios.post("hand_sign/hand_img_upload", data).then((res) => {
        console.log(res);
        this.$Message.notify({
          content: "",
          type: "success",
          //time: 5500
        });
      });
    },
    

    // base64Blob 
    base64ToBlob(base64Data) {
      let arr = base64Data.split(","),
        fileType = arr[0].match(/:(.*?);/)[1],
        bstr = atob(arr[1]),
        l = bstr.length,
        u8Arr = new Uint8Array(l);

      while (l--) {
        u8Arr[l] = bstr.charCodeAt(l);
      }
      return new Blob([u8Arr], {
        type: fileType,
      });
    },
  },
};
</script>
<style lang="css">
.boardBox {
  width: 100vw;
  height: 100vh;
  overflow: hidden;
}
#canvas {
  background-color: #fff;
}
.footer-bar {
  background-color: #f6fafd;
  box-shadow: 0 4px 8px 0 rgba(7, 17, 27, 0.1);

}
.footer-bar div {
  padding: 0.6rem 1.5rem;
}
.footer-bar div.reset {
  color: #333;
}
.footer-bar div.use {
  background-color: #20a0ff;
  color: #fff;
}
/**/
@media only screen and (orientation: portrait) {
  .sign-wrap {
    display: flex;
    align-items: center;
  }
  .footer-bar {
    position: fixed;
    right: 0;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    box-sizing: border-box;
    padding: 4rem 0;
    background-color: #f6fafd;
  }
  .footer-bar div {
    /* margin: .5rem; */
    transform: rotate(-90deg);
  }
}

/**/
@media only screen and (orientation: landscape) {
  .footer-bar {
    position: fixed;
    bottom: 0;
    width: 100vw;
    display: flex;
    justify-content: space-between;
    align-items: center;
    /* padding: 0 0.4rem; */
    padding: 0 4rem;
    box-sizing: border-box;
    box-shadow: 0px 0px 8px rgba(7, 17, 27, 0.1);
  }
  .footer-bar div {
    margin: 0.8rem 0;
  }
}
</style>
